import React from 'react';
import sprite from '../../img/svg-sprite/sprite.svg';
import { useForm } from 'react-hook-form';
import { StyledLoginWithPhone } from './LoginWithPhone.styled';

const LoginWithPhone = () => {
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm({
    mode: 'onChange',
    defaultValues: {
      phone_number: '',
    },
  });

  const onSubmit = data => {
    console.log(data);
    reset();
  };

  return (
    <StyledLoginWithPhone>
      <form onSubmit={handleSubmit(onSubmit)}>
        <label className="login-label">
          
          <div className="login__country-code">+38</div>
          <input
            className="login-input phone-input"
            type="number"
            {...register('phone_number', {
              pattern: {
                value: /^[0-9]{10}$/,
                message: '     +380 __ ___ __ __',
              },
              required: ` ,    `,
              maxLength: { value: 10, message: `  13 ` },
              minLength: {
                value: 10,
                message: `    +380 __ ___ __ __`,
              },
            })}
            aria-invalid={errors.phone_number ? 'true' : 'false'}
          />
        </label>
        {errors.phone_number && (
          <p role="alert" className="login-error">
            {errors.phone_number.message}
          </p>
        )}

        <div className="button-checkbox-container">
          <button type="submit" className="login-button">
              
          </button>
          <svg className="login-arrow" width="36px" height="36px">
            <use href={sprite + '#arrow'} />
          </svg>
        </div>
      </form>
    </StyledLoginWithPhone>
  );
};

export default LoginWithPhone;
